import * as okayapi from '../../../components/okayapi/okayapi.js';


export default {
	data() {
		return {
			title: 'pinyin',
			first: '',
			abbr: '',
			second: '',
			convert: '',
			third: '',
			name: '',
			fourth: '',
			sentence: '',
		}
	},
	methods: {
		abbrOkay: function() {
			var params = {
				s: "Ext.Pinyin.Abbr", // 必须，待请求的接口服务名称
				text: this.abbr,
			};
			uni.request({
				url: this.okayapiHost,
				method: 'GET',
				data: okayapi.enryptData(params),
				success: res => {
					this.first = res.data.data.pinyin
				},
				fail: res => {
					console.log(res);
				},
				complete: () => {}
			});
		},
		convertOkay: function() {
			var params = {
				s: "Ext.Pinyin.Convert", // 必须，待请求的接口服务名称
				text: this.convert,
			};
			uni.request({
				url: this.okayapiHost,
				method: 'GET',
				data: okayapi.enryptData(params),
				success: res => {
					this.second = res.data.data.pinyin
				},
				fail: res => {
					console.log(res);
				},
				complete: () => {}
			});
		},
		nameOkay: function() {
			var params = {
				s: "Ext.Pinyin.Name", // 必须，待请求的接口服务名称
				text: this.name,
			};
			uni.request({
				url: this.okayapiHost,
				method: 'GET',
				data: okayapi.enryptData(params),
				success: res => {
					this.third = res.data.data.pinyin
				},
				fail: res => {
					console.log(res);
				},
				complete: () => {}
			});
		},
		sentenceOkay: function() {
			var params = {
				s: "Ext.Pinyin.Sentence", // 必须，待请求的接口服务名称
				text: this.sentence,
			};
			uni.request({
				url: this.okayapiHost,
				method: 'GET',
				data: okayapi.enryptData(params),
				success: res => {
					this.fourth = res.data.data.pinyin
				},
				fail: res => {
					console.log(res);
				},
				complete: () => {}
			});
		},
	}
}
